import React, { useContext, useEffect, useState } from 'react'
import TopRatedMovies from './TopRatedMovies'
import headerImage from "../images/footer-bg.jpg";
import { Context } from '../Context/MoviesContext';
import "./Movies.css"
import { Link } from 'react-router-dom';
import TrendingMovies from './TrendingMovies';

export default function Movies() {
  const [Text, setText] = useState(null);
  let {getMovieWord,SearchMovie}= useContext(Context);

  useEffect(() => {
    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
  }, [SearchMovie])

 let Search=(e)=>{
    e.preventDefault();
    if(Text!=null){
      getMovieWord(Text)
    }
  }

 let getText=(e)=>{
    setText(e.target.value)
  }

  return (<>
   <div className='headerImage text-center py-5' style={{backgroundImage:`url(${headerImage})`}}>
            <h3 className='text-white pt-5 pb-3 '>Movies</h3>
    </div>
    <div className="topMovie">
      <div className="searchBtn">
        <form onSubmit={Search} >
              <input onChange={getText} className='bg-black text-muted' type="text" placeholder='Enter Keyword' />
              <button className='btn px-4'>Search</button>
        </form>
      </div>
      {/* Searched movies */}
      {SearchMovie.length>0?<section className='movies'>
<div className=" py-5">
<div className='container '>
  <div className="row g-3">
    {SearchMovie.map((item,index)=> <div className='col-md-3' key={index}>
      <Link to={`/details/${item.id}/movie`}>
        <div className='movieImage'>
            <img className='w-100 text-white' src={`https://image.tmdb.org/t/p/original${item.poster_path}`} alt="image" />
        </div>
      </Link>
        <div className="movieName text-white text-center mt-2">
          <h4>{item.title}</h4>
        </div>
    </div>)}
  </div>
</div>
</div>
  {/* Movies List */}
</section>: <TrendingMovies/>}
    </div>
    </>)
}
