import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import {Swiper, SwiperSlide}from 'swiper/react';
import 'swiper/css/effect-fade'
import { Context } from '../Context/MoviesContext';
import Footer from '../Footer/Footer';
import AOS from "aos";
import 'aos/dist/aos.css';
import '../Home/Home.css'



export default function Home() {
  let Navigate = useNavigate();
  const [data, setData] = useState([]);

  let {topRateMovies,trendingMovies,trendingTV,topRateTV} = useContext(Context);

  useEffect(() => {
  //animation for header
    AOS.init({
    });
    //get API data
    getData();
    // scrolling top when switch pages
    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
  }, []);


  let getData =async ()=>{
    let {data} = await axios.get('https://api.themoviedb.org/3/trending/movie/week?api_key=f9250279af7dc881bf30759ec5164577')
    setData(data.results.slice(12,17));
  }
  let gotoMovies = ()=>{
    Navigate('/movies')
  }
  let gotoTVs = ()=>{
    Navigate('/tv')
  }
  return (<>
<Swiper grabCursor={true}  >
    {data?data.map((item,index)=><SwiperSlide  key={index}>
    {({ isActive }) => (
      <div>{isActive ?  <div className="swiper-slide main-slider"  style={{backgroundImage:`url(https://image.tmdb.org/t/p/original${item.backdrop_path})`}}>
      <div className="backgroundLayer">
      <div className="container-fluid header-content">
        <div className="row pt-5 mx-4">
     <div className="col-lg-6  text-white fw-bold">
           <h1 data-aos='fade-down' data-aos-offset={0} data-aos-delay={500} className='movieName py-4'>{item.title.split(" ").slice(0,3).join(" ")}</h1>
            <p data-aos="fade-down" data-aos-offset={0} data-aos-delay={700}>{item.overview.split(" ").slice(0,40).join(" ")}</p>
            <div data-aos="fade-down" data-aos-offset={0} data-aos-delay={900} className="btns mt-5">
              <Link to={`/details/${item.id}/movie`}>
                <button className='watchNow btn my-3 px-4 '>Watch now</button>
              </Link>
              <Link to={`/details/${item.id}/movieTrailer`}>                 
                 <button data-bs-toggle="modal" data-bs-target="#exampleModal" className='watchTrailer my-3 btn px-4 '>Watch trailer</button>
              </Link>
            </div>
         </div>
         
          <div className="col-md-5 d-lg-block d-none m-4">
            <img data-aos="zoom-in" data-aos-delay={200} className='movie-image rounded-5' src={`https://image.tmdb.org/t/p/original${item.poster_path}`} alt="" />
          </div>
        </div>
      </div>
      </div>
  </div> : 'not active'}</div>
    )}
       
     
      </SwiperSlide>
    ):''}
</Swiper>       

<div className='sliders'>
  {/* trending Movies Slider */}
<div className="SliderTitle">
    <h3>Trending Movies</h3>
    <button onClick={gotoMovies}>View More</button>
  </div>
  <Swiper  breakpoints={{
    0:{
      slidesPerView:1.2,
      spaceBetween:10},
    480:{
      slidesPerView:2.2,
      spaceBetween:10},
    768:{
      slidesPerView:3.5,
      spaceBetween:10},
    1024:{
      slidesPerView:5.5,
      spaceBetween:10},
    1280:{
      slidesPerView:6.2,
      spaceBetween:10},
      }}>
    {trendingMovies?trendingMovies.map((item,index)=><SwiperSlide key={index}>
      <div className='movieSlider'>
      <Link to={`/details/${item.id}/movie`}>
        <img className='w-100' src={`https://image.tmdb.org/t/p/original${item.poster_path}`} alt="" />
        <div className="movieLayer ">
          <i className="youtubeIcon fa-brands fa-youtube"></i>
        </div>
      </Link>
      </div>
      <h5 className='text-white text-center py-3'>{item.title}</h5>
  </SwiperSlide>):''}
  </Swiper>

  {/* Top Rated Movies Slider*/}

  <div className="SliderTitle">
    <h3>Top Rated Movies</h3>
    <button onClick={gotoMovies}>View More</button>
  </div>
  <Swiper  breakpoints={{
    0:{
      slidesPerView:1.2,
      spaceBetween:10},
    480:{
      slidesPerView:2.2,
      spaceBetween:10},
    768:{
      slidesPerView:3.5,
      spaceBetween:10},
    1024:{
      slidesPerView:5.5,
      spaceBetween:10},
    1280:{
      slidesPerView:6.2,
      spaceBetween:10},
      }}>
    {topRateMovies?topRateMovies.map((item,index)=><SwiperSlide key={index}>
      <div className='movieSlider'>
        <Link to={`/details/${item.id}/movie`}>
          <img className='w-100' src={`https://image.tmdb.org/t/p/original${item.poster_path}`} alt="" />
          <div className="movieLayer ">
            <i className="youtubeIcon fa-brands fa-youtube"></i>
          </div>
        </Link>
      </div>
      <h5 className='text-white py-3 text-center'>{item.title}</h5>
  </SwiperSlide>):''}
  </Swiper>
  {/* trending TV Slider */}
  <div className="SliderTitle">
    <h3>Trending TV</h3>
    <button onClick={gotoTVs}>View More</button>
  </div>
  <Swiper  breakpoints={{
    0:{
      slidesPerView:1.2,
      spaceBetween:10},
    480:{
      slidesPerView:2.2,
      spaceBetween:10},
    768:{
      slidesPerView:3.5,
      spaceBetween:10},
    1024:{
      slidesPerView:5.5,
      spaceBetween:10},
    1280:{
      slidesPerView:6.2,
      spaceBetween:10},
      }}>
    {trendingTV?trendingTV.map((item,index)=><SwiperSlide key={index}>
      <div className='movieSlider '>
      <Link to={`/details/${item.id}/tv`}>
        <img className='w-100' src={`https://image.tmdb.org/t/p/original${item.poster_path}`} alt="" />
        <div className="movieLayer ">
          <i className="youtubeIcon fa-brands fa-youtube"></i>
        </div>
      </Link>
      </div>
      <h5 className='text-white py-3 text-center'>{item.title}</h5>
  </SwiperSlide>):''}
  </Swiper>
  {/* topRate TV  Slider */}
  <div className="SliderTitle">
    <h3>Top Rate TV</h3>
    <button onClick={gotoTVs}>View More</button>
  </div>
  <Swiper  breakpoints={{
    0:{
      slidesPerView:1.2,
      spaceBetween:10},
    480:{
      slidesPerView:2.2,
      spaceBetween:10},
    768:{
      slidesPerView:3.5,
      spaceBetween:10},
    1024:{
      slidesPerView:5.5,
      spaceBetween:10},
    1280:{
      slidesPerView:6.2,
      spaceBetween:10},
      }}>
    {topRateTV?topRateTV.map((item,index)=><SwiperSlide key={index}>
      <div className='movieSlider '>
      <Link to={`/details/${item.id}/tv`}>
        <img className='w-100' src={`https://image.tmdb.org/t/p/original${item.poster_path}`} alt="" />
        <div className="movieLayer ">
          <i className="youtubeIcon fa-brands fa-youtube"></i>
        </div>
      </Link>
      </div>
      <h5 className='text-white py-3 text-center'>{item.title}</h5>
  </SwiperSlide>):''}
  </Swiper>



</div>


<Footer/>
  
  </>)
}
