import React from 'react'
import { Link } from 'react-router-dom';
import "./Footer.css"
import footerImg from "../images/footer-bg.jpg";
import logo from '../images/tmovie.png';

export default function Footer() {
  return (<>
    <footer className='footer' style={{backgroundImage:`url(${footerImg})`}}>
        <div className="container text-center py-5">
            <a className="navbar-brand fs-1" href="#"><img src={logo} width={50} height={50} alt="Logo" /> tMovies</a>
            <div className="row gx-3 py-5 text-start">
                <div className="col-4">
                    <p>Home</p>
                    <p>Contact us</p>
                    <p>Term of services</p>
                    <p>About us</p>
                </div>
                <div className="col-4">
                    <p>Live</p>
                    <p>FAQ</p>
                    <p>Premium</p>
                    <p>Pravacy policy</p>
                </div>
                <div className="col-md-4">
                    <p>You must watch</p>
                    <p>Recent release</p>
                    <p>Top IMDB</p>
                </div>
            </div>
        </div>
    </footer>
  </>)
}
