import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import '../Details/Details.css'
import axios from 'axios';
import SimilarMovies from '../Similar/SimilarMovies';
import Footer from '../Footer/Footer';

export default function Details() {
    const [data, setdata] = useState([]);
    const [genres, setgenres] = useState([]);
    const [casts, setcasts] = useState([]);
    const [videos, setvideos] = useState([]);
    const [trailer, setTrailer] = useState(false)
    let {id,mediaType} = useParams();
    useEffect(() => {
      if(mediaType==='movie')
      {
        getDetails(id,'movie');
        getCasts(id,'movie');
      }
      else if(mediaType==='tv')
      {
        getDetails(id,'tv');
        getCasts(id,'tv');
      }
      else if(mediaType==='movieTrailer')
      {
        setTrailer(true)
        getDetails(id,'movie');
      }
     
      window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
    }, [id,mediaType])
    
    let getDetails =async(id,media)=>{
        let {data}=await axios.get(`https://api.themoviedb.org/3/${media}/${id}?api_key=f9250279af7dc881bf30759ec5164577&append_to_response=videos,images`)
        setdata(data);
        setgenres(data.genres); 
        if(mediaType==='movieTrailer')
        {
          setvideos([data.videos.results[data.videos.results.length-1]]);
        }
        else{
          setvideos([...data.videos.results.slice(0,5),data.videos.results[data.videos.results.length-1]]);
        }
  
      }
    let getCasts = async(id,media)=>{
      let {data} = await axios.get(`https://api.themoviedb.org/3/${media}/${id}/credits?api_key=f9250279af7dc881bf30759ec5164577&language=en-US`)
      setcasts(data.cast.slice(0,6));
    }

  return (<>
 <section className='AllDetails'>
  
  {trailer? videos.map((video,index)=><div className="trailar" key={index} >
    <div className="row">
        <div className='video mt-5 col-12'> 
                        <h3 className='text-white'>{video.name}</h3>
                        <iframe className='' src={`https://www.youtube.com/embed/${video.key}`} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
      </div>
    </div>
  </div>
 )  :<>
  {data?<><div className="bgImage " style={{backgroundImage:`url(https://image.tmdb.org/t/p/original${data.backdrop_path})`}}>
   <div className="colorLayer"></div>
 </div>
 <div className="bgColor">
   <div className="Content position-relative">
       <div className="container text-white pt-5">
           <div className="row mt-5">
               <div className="col-4">
                 <img className='w-100 rounded-5' src={`https://image.tmdb.org/t/p/original${data.poster_path}`} alt="" />
               </div>
               <div className="col-md-8">
                 <h1>{data.title}</h1>
                 <div className="d-flex justify-content-start align-items-center">
                   {genres?genres.map((genre,index)=><div className="genre m-2" key={index}>
                     <p>{genre.name}</p>
                   </div>
                   ):''}
                 </div>
                 <div className="movieDescription">
                   <p>{data.overview}</p>
                 </div>
                 <div className="casts">
                   <h3>Casts</h3>
                   <div className="persons row">
                     {casts?casts.map((person,index)=><div className='col-lg-2 col-3 g-3 text-center' key={index}>
                         <img className='w-100 mb-2'  src={`https://image.tmdb.org/t/p/original${person.profile_path}`} alt="" />
                         <h5>{person.name}</h5>
                     </div>):''}
                   </div>
                 </div>
               </div>
           </div>
       </div>
       <div className="container-fluid">
        <div className='videos row mt-3 g-5'>
                    {videos?videos.map((video,index)=><div className='video col-12 text-center' key={index}>
                      <h3 className='text-white'>{video.name}</h3>
                    <iframe className='' src={`https://www.youtube.com/embed/${video.key}`} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>                              </div>):''}
        </div>
       </div>
      
       <div className="similar text-white mt-5 mx-4">
         <h3>Similar</h3>
         <SimilarMovies/>
       </div>
     
   </div>
 </div>
 <Footer/>
 </>:''}
  </>}


 


</section>
    
  </>)
}
