import axios from "axios";
import { createContext, useEffect, useState } from "react";

export let Context = createContext(0);


export default function MoviesProvider (props){
    const [topRateMovies, settopRateMovies] = useState([]);
    const [topRateTV, settopRateTV] = useState([]);
    const [trendingMovies, setTrendingMovies] = useState([]);
    const [trendingTV, setTrendingTV] = useState([]);
    const [trendingPerson, settrendingPerson] = useState([]);
    const [SearchMovie, setSearchMovie] = useState([]);
    const [SearchTV, setSearchTV] = useState([]);
    const [MoreMovies, setMoreMovies] = useState([]);
    const [MoreTV, setMoreTV] = useState([]);
    useEffect(() => {
      getTopRated('movie', settopRateMovies);
      getTopRated('tv', settopRateTV);
      getTrending('movie',setTrendingMovies);
      getTrending('tv',setTrendingTV);
      getTrending('person',settrendingPerson);
    }, []);
  
    let getTopRated =async(mediaType,callback)=>{
      let {data} = await axios.get(`https://api.themoviedb.org/3/${mediaType}/top_rated?api_key=f9250279af7dc881bf30759ec5164577&language=en-US&page=1`)
      callback(data.results)
    }

    let getTrending=async(mediaType,callback,Pages=1)=>{
        let {data} = await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/week?api_key=f9250279af7dc881bf30759ec5164577&language=en-US&page=${Pages}`)
          callback(data.results);
          if(callback==setMoreMovies)
          {
            setMoreMovies([...MoreMovies,...data.results]);
            console.log(MoreMovies);
          }
          if(callback==setMoreTV)
          {
            setMoreTV([...MoreTV,...data.results]);
          }
         
    }
    let search=async(media,text)=>{
        
      let {data} = await axios.get(`https://api.themoviedb.org/3/search/${media}?api_key=f9250279af7dc881bf30759ec5164577&language=en-US&page=1&include_adult=false&query=${text}`)
       if(media==='movie')
       {
        setSearchMovie(data.results);
       }
       if(media==='tv')
       {
        setSearchTV(data.results);
       }
     
      } 

      let incrementMovies=(Pages)=>{
        getTrending("movie",setMoreMovies,Pages);
      }

      let incrementTV=(Pages)=>{
        getTrending("tv",setMoreTV,Pages);
      }

      let getMovieWord=(text)=>{
          search('movie',text);
      }
 
    let getTvWord=(text)=>{
      search('tv',text);
   }
    return <Context.Provider value={{topRateMovies,trendingMovies,trendingTV,topRateTV,getMovieWord,getTvWord,SearchMovie,SearchTV,incrementMovies,incrementTV,MoreMovies,MoreTV}}>
        {props.children}
    </Context.Provider>
}